.gg-spinner {
  transform: scale(var(--ggs, 1));
}
.gg-spinner,
.gg-spinner::after,
.gg-spinner::before {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: 50px;
  height: 50px;
}
.gg-spinner::after,
.gg-spinner::before {
  content: "";
  position: absolute;
  border-radius: 100px;
}
.gg-spinner::before {
  animation: spinner 1s cubic-bezier(0.6, 0, 0.4, 1) infinite;
  border: 3px solid transparent;
  border-top-color: currentColor;
}
.gg-spinner::after {
  border: 3px solid;
  opacity: 0.2;
}
@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}

.loading {
  text-align: center;
  font-size: 2rem;
  display: block;
  margin: auto;
  margin-top: 25%;
  width: 50%;
}

.loader-img {
  display: block;
  margin: auto;
}
